import useNotifiStore from '@cetus/stores/src/notifi'
import { d } from '@cetus/utils'
import { useGetOwnerVeNFT } from '../../../../web/src/hooks/xcetus/useXCetusHelper'
import { useGlobalToast } from '../../hooks'

export function useNotifiHelper() {
  const { fetchOwnerVeNFT } = useGetOwnerVeNFT()
  const { showCommonToast } = useGlobalToast()
  const { notifiSources } = useNotifiStore()

  const { setNotifiSubscriptionLoading } = useNotifiStore()

  // è®¢é˜…éªŒè¯é€»è¾‘
  const createNotifiSubscriptionVerify = async () => {
    try {
      const xCetusVenftInfo = await fetchOwnerVeNFT()
      if (xCetusVenftInfo?.xcetus_balance) {
        const balance = d(xCetusVenftInfo.xcetus_balance).div(Math.pow(10, 9))
        if (balance.lessThan(1000)) {
          showCommonToast('Price alerts are currently open to users who hold 1000+ xCETUS.', 'rejected')
          return false
        }
      } else {
        showCommonToast('Unable to fetch xCetus information.', 'rejected')
        return false
      }

      if (notifiSources.length >= 5) {
        showCommonToast('Price alerts can only be enabled for up to 5 positions for an account at the same time.', 'rejected')
        return false
      }

      return true
    } catch (error) {
      console.error('ðŸš€ ~ createNotifiSubscriptionVerify ~ error:', error)
      setNotifiSubscriptionLoading(false)
      showCommonToast('An error occurred while verifying the subscription.', 'rejected')
      return false
    }
  }

  // èŽ·å–æŒ‡å®šä½ç½®çš„é€šçŸ¥çŠ¶æ€
  const getPositionNotifiStatus = (posId: string, posIndex: string) => {
    console.log('ðŸš€ðŸš€ðŸš€ ~ useNotifiHelper.ts:46 ~ getPositionNotifiStatus ~ notifiSources:', notifiSources)
    try {
      const currentSource = notifiSources.find(source => {
        const { pool_address, position_index } = source
        return pool_address === posId && position_index === posIndex
      })

      return {
        isSubscription: Boolean(currentSource),
        alertID: currentSource?.alertID || ''
      }
    } catch (error) {
      console.error('ðŸš€ ~ getPositionNotifiStatus ~ error:', error)
      return { isSubscription: false, alertID: '' }
    }
  }

  return { createNotifiSubscriptionVerify, getPositionNotifiStatus }
}
